import { VideoCameraOutlined, FileImageOutlined, UserOutlined, SettingOutlined, GroupOutlined  } from "@ant-design/icons";
import { Layout, Menu, theme, Avatar, Button } from "antd";
import { Outlet, useNavigate } from "react-router-dom";
import React from "react";

const { Header, Content, Footer, Sider } = Layout;

const MainLayout = (userDetails) => {
  const navigate = useNavigate();

  const user = userDetails.user;
  const {token: { colorBgContainer }} = theme.useToken();

  const manuItems = [
    // {key: 1, icon: React.createElement(UserOutlined), label: "Profile"},
    {key: 1, icon: React.createElement(FileImageOutlined), label: "Pictures"},
    {key: 2, icon: React.createElement(VideoCameraOutlined), label: "Videos"},
    {key: 3, icon: React.createElement(GroupOutlined), label: "Albums"},
    {key: 4, icon: <SettingOutlined />, label: "Setttings"}
     
  ];
  
  const logout = () => {
    navigate('/login');
    window.open(process.env.REACT_APP_API_URL+'/api/google/auth/logout', "_self");
  };

  return (<Layout>
    <Sider breakpoint="lg" collapsedWidth="0" onBreakpoint={(broken) => {console.log(broken);}} onCollapse={(collapsed, type) => {console.log(collapsed, type);}}>
      <div className="text-center mb-3">
        <div className="mt-5">
        {
          user ? 
          <Avatar size={80} icon={<SettingOutlined />} style={{backgroundColor: '#ffffff40'}}/>
          :
          <Avatar size={80} icon={<UserOutlined />} style={{backgroundColor: '#ffffff40'}}/>
        }
        </div>
        <Button className="mt-3" type="primary" onClick={logout}>Logout</Button>
      </div>
      <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]} items={manuItems}/>
    </Sider>
    <Layout>
      <Header style={{padding: 0, background: colorBgContainer}}></Header>
      <Content>
        <Outlet />
      </Content>
      <Footer style={{textAlign: "center"}}>Kurunagai ©2023 Created by Kurunagai Tech.</Footer>
    </Layout>
  </Layout>);
};
export default MainLayout;