import { LockOutlined, UserOutlined, GoogleOutlined } from '@ant-design/icons';
import { notification, Row, Col, Input, Button, Card, Form, Checkbox } from 'antd';
import { useNavigate } from "react-router-dom";
import React from 'react';

const Login = () => {
  const navigate = useNavigate();

  const googleAuth = () => {
		window.open( process.env.REACT_APP_API_URL+'/api/google/auth/callback', "_self");
	};

  const onFinish = (values) => {
    navigate('/home');
    openNotification('warning','Login','Direct login is suspended at this moment. Please use Google sign in.');
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
    openNotification('error','Login',errorInfo)
  };

  const openNotification = (type, title, message) => {
    const key = `open${Date.now()}`;
    notification.open({
		message: title,
		description: message,
		key,
		type,
		duration:20,
		onClose: console.log('Notification Closed.')});
  };

  return (<div className='login-page'>
  	<Card className='login-card'>
      <div className='login-head'>Login</div>
      <Form name="normal_login" className="login-form" initialValues={{remember: true}} onFinish={onFinish} onFinishFailed={onFinishFailed}>
        <Form.Item name="username" rules={[{required: true, message: 'Please input your username!'}]}>
          <Input placeholder="Username" prefix={<UserOutlined className="site-form-item-icon" />}/>
        </Form.Item>
        <Form.Item name="password" rules={[{required: true, message: 'Please input your password!'}]}>
          <Input.Password placeholder="Password" prefix={<LockOutlined className="site-form-item-icon" />} type="password"/>
        </Form.Item>
        <Form.Item style={{margin: 0}}>
          <Form.Item name="remember" valuePropName="checked">
            <Checkbox>Remember me</Checkbox>
          </Form.Item>
          <a className="login-form-forgot" href="">Forgot password</a>
        </Form.Item>
        <Form.Item>
          <Row gutter={12}>
            <Col span={12}><Button type="primary" htmlType="submit" className="login-form-button">Login</Button></Col>
            <Col span={12}><Button type="primary" className="login-form-button" onClick={()=> navigate('/signup')}>SignUp</Button></Col>
          </Row>
        </Form.Item>
      </Form>
      <Button icon={<GoogleOutlined className="site-form-item-icon"/>} type="primary" onClick={googleAuth} className="login-form-button">Sign in with Google</Button>
      <div className='text-center mt-2'>Kurunagai ©2023 Created by Kurunagai Tech.</div>
	  </Card>
  </div>);
};
export default Login;