import { LockOutlined, UserOutlined, GoogleOutlined, MailOutlined } from '@ant-design/icons';
import { theme, notification, Input, Button, Card, Form } from 'antd';
import { Link, useNavigate } from "react-router-dom";
import React from 'react';

const Signup = () => {
  const navigate = useNavigate();
  const { token: { colorBgContainer } } = theme.useToken();

  const googleAuth = () => {
		window.open( process.env.REACT_APP_API_URL+'/api/google/auth/callback', "_self");
	};

  const onFinish = (values) => {
    navigate('/home');
    openNotification('Login Form','Direct login is suspended at this moment. Please use Google sign in.');
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  const openNotification = (title, message) => {
    const key = `open${Date.now()}`;
    notification.open({ message: title, description: message, key, onClose: console.log('Notification Closed.')});
  };

  return (<div className='login-page' style={{bgcolor:{colorBgContainer}}}>
  	<Card className='login-card'>
      <div className='login-head'>Create Account</div>
      <Form name="normal_login" className="login-form" initialValues={{remember: true}} onFinish={onFinish} onFinishFailed={onFinishFailed}>
        <Form.Item name="username" rules={[{required: true, message: 'Please input your username!'}]}>
          <Input placeholder="Username" prefix={<UserOutlined className="site-form-item-icon" />}/>
        </Form.Item>
		<Form.Item name="email" rules={[{required: true, message: 'Please input your email!'}]}>
          <Input placeholder="Email" prefix={<MailOutlined className="site-form-item-icon" />}/>
        </Form.Item>
        <Form.Item name="password" rules={[{required: true, message: 'Please input your password!'}]}>
          <Input.Password placeholder="Password" prefix={<LockOutlined className="site-form-item-icon" />} type="password"/>
        </Form.Item>
        <Form.Item className='mb-2'>
			<Button type="primary" className="login-form-button" onClick={()=> navigate('/signup')}>SignUp</Button>
        </Form.Item>
      </Form>
	  <div className='text-center mb-2'>Or</div>
      <Button icon={<GoogleOutlined className="site-form-item-icon"/>} type="primary" onClick={googleAuth} className="login-form-button">Sign up with Google</Button>
	  <p>Already have account ? <Link to="/login">Login</Link></p>
	  <div className='text-center mt-2'>Kurunagai ©2023 Created by Kurunagai Tech.</div>
	</Card>
  </div>);
};
export default Signup;